import axios from "axios";

const url = "router/";

class ApiCalls {
  static getProjects() {
    return new Promise((resolve, reject) => {
      axios
        .get(url)
        .then((res) => {
          const data = res.data;
          resolve(
            data.map((project) => ({
              ...project,
            }))
          );
        })
        .catch((err) => {
          reject(err);
        });
    });
  }

  //Create Post
  static insertProject(data) {
    return axios.post(url, data);
  }

  //Delete Post
  static deleteProject(id) {
    return axios.delete(`${url}${id}`);
  }
}

export default ApiCalls;
